<template>
    <div class="loadingmain">
        <section>
            <h3 class="title"></h3>
            <el-input v-model="input" placeholder="" style="width: 400px;"></el-input>
        </section>
        <section>
          <!--<div class="projectselect">
            <span></span>
            <el-select v-model="key1" placeholder="..." @change="getPoint(key1)">
              <el-option
                v-for="item in chapterData"
                :key="item.id"
                :key1="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>-->
          <div class="projectselect">
            <span></span>
            <el-select v-model="type" placeholder="" class="select-type" @change="selectQuestionType">
              <el-option label="" value="10001" selected="selected"></el-option>
              <el-option label="" value="10002"></el-option>
              <el-option label="" value="10003"></el-option>
              <el-option label="" value="10004"></el-option>
              <el-option label="" value="10005"></el-option>
            </el-select>
          </div>
         <!-- <div class="projectselect">
            <span></span>
            <el-select v-model="key2" placeholder="..." @focus="getChapter" @change="getPointItem(key2)">
              <el-option
                v-for="item in pointData"
                :key="item.id"
                :key2="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>-->

            <el-table :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)" border stripe v-loading="loading" style="width: 100%;margin-top:10px" max-height="449" highlight-current-row :cell-style="styleFunc">
                <el-table-column prop="id" label="ID" width="60" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="question" label="" width="350" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="optioncontent" label="" width="250" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="answer" label="" width="250" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="score" label="" width="80" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="usageCount" label="" width="80" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="faq" label="" width="150" align="center" show-overflow-tooltip>
                </el-table-column>
                <el-table-column label="" width="150" align="center">
                    <template slot-scope="scope">
                                <el-button  size="mini" type="success" @click="selectItem(scope.row)"><i class="el-icon-plus"></i></el-button>
</template>
            </el-table-column>
        </el-table>

         <!--  -->
          <div class="block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[5, 10, 20, 30]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="count">
            </el-pagination>
          </div>
        </section>
        <hr>
        <h3 class="title"></h3>
        <el-tabs v-model="activeName"  type="border-card">
            <el-tab-pane label="" name="first">
                <selectproject1></selectproject1>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
                <selectproject2></selectproject2>
            </el-tab-pane>
            <el-tab-pane label="" name="third">
                <selectproject3></selectproject3>
            </el-tab-pane>
            <el-tab-pane label="" name="fourth">
                <selectproject4></selectproject4>
            </el-tab-pane>
            <el-tab-pane label="" name="fifth">
                <selectproject5></selectproject5>
            </el-tab-pane>
        </el-tabs>
        <p class="product"><el-button type="primary" size="medium" @click="productProtect()"></el-button></p>
        <lookInfo></lookInfo>
    </div>
</template>

<script>
    import Vue from 'vue'
    import bus from './BUS.vue'
    import {
        Loading
    } from 'element-ui';
    import lookInfo from './lookInfo.vue'
    import selectproject1 from './selectproject1.vue'
    import selectproject2 from './selectproject2.vue'
    import selectproject3 from './selectproject3.vue'
    import selectproject4 from './selectproject4.vue'
    import selectproject5 from './selectproject5.vue'
    export default {
        data() {
            return {
              pageSize:5,
              key1:'',
              key2:'',
              key3:'',
              pointData:[],
              chapterData:[
                {
                  "id":1,
                  "value":" "
                },
                {
                  "id":2,
                  "value":" "
                },
                {
                  "id":3,
                  "value":" "
                },
                {
                  "id":4,
                  "value":" "
                },
                {
                  "id":5,
                  "value":" "
                },
                {
                  "id":6,
                  "value":" "
                },
                {
                  "id":7,
                  "value":" "
                },
                {
                  "id":8,
                  "value":" CPU"
                },{
                  "id":9,
                  "value":" "
                },
                {
                  "id":10,
                  "value":" "
                }
              ],
              typeData:[
                {
                  "id":10001,
                  "value":""
                },
                {
                  "id":10002,
                  "value":""
                },
                {
                  "id":10003,
                  "value":""
                },
                {
                  "id":10004,
                  "value":""
                },
                {
                  "id":10005,
                  "value":""
                }
              ],
                input: '',
                userInfo: {},
                type: '',
                currentPage: 1,
                count: 0,
                tableData: [],
                loading: false,
                activeName: 'first', //
                //
                singleSelect: '', //
                multipleSelect: '', //
                fillblank: '', //
                tureorfalse: '', //
                quesAndAns: '' //
            }
        },
        methods: {
          getChapter(){
            if(this.key1 == ''){
              this.$message.error('');
            }
          },
          getPoint(val){
            this.axios.get('http://localhost:8089/point/getPoint',{
              params:{
                chapterId:val
              }
            })
              .then(res =>{
                this.pointData = [];
                this.pointData = res.data.data;
              })
          },
            selectQuestionType(v) {
                this.type = v
            },
          getPointItem(val){
            if(this.key3 == ''){
              this.$message.error('');
              this.key2 = '';
            }
            else{
              this.axios.get('http://localhost:8089/chapter/getPointItems',{
                params:{
                  type:this.key3,
                  point:val
                }
              })
                .then(res =>{
                  this.tableData = [];
                  this.tableData = res.data.data;
                  this.count = res.data.data.length;
                })
                .catch(res =>{
                  console.log('error');
                })
            }
          },
            getQuestionHandle() {
                let params = new URLSearchParams();
                params.append("type", this.type);
                this.axios.get('http://localhost:8089/paperorganize/getQuestions', {params: {
                type:this.type}
                })
                    .then(res => {
                        this.tableData = res.data.data;
                        this.count = res.data.data.length;
                        this.currentPage =1;
                        this.loading = false;
                    })
                    .catch(res => {
                        this.loading = false;
                        console.log("error");
                    })
            },
            reset() { //
                for (var val in this.select) {
                    this.select[val] = 0;
                }
            },
            selectItem(param) { //
                var number = Number(this.type);
                if (number === 10001) {
                    bus.$emit("addData1", param);
                } else if (number === 10002) {
                    bus.$emit("addData2", param);
                } else if (number === 10003) {
                    bus.$emit("addData3", param);
                } else if (number === 10004) {
                    bus.$emit("addData4", param);
                } else if (number === 10005) {
                    bus.$emit("addData5", param);
                }
            },
            handleCurrentChange(val) { //,
                let _this = this
                _this.loading = true;
                setTimeout(() => {
                    _this.loading = false
                }, 500)
                this.currentPage = val;
            },
          handleSizeChange(val){
            this.pageSize = val;
          },
            styleFunc(row, rowIndex) { //
                return {
                    padding: '5px 0',
                    hieght: '23px',
                    lineHeight: '23px'
                }
            },
            productProtect() { //
                bus.$emit('needids', true);
                let resObj = {'userid': Number(this.userInfo.username)}
                resObj.name = this.input
                resObj.singleSelect = this.singleSelect.map(v => Number(v))
                resObj.multipleSelect = this.multipleSelect.map(v => Number(v))
                resObj.fillblank = this.fillblank.map(v => Number(v))
                resObj.tureorfalse = this.tureorfalse.map(v => Number(v))
                resObj.quesAndAns = this.quesAndAns.map(v => Number(v))
                if(this.input.length == 0){
                    this.$message.error('');
                    return false
                }

                if(this.multipleSelect.length == 0 || this.fillblank.length == 0 || this.tureorfalse.length == 0 || this.quesAndAns == 0){
                    this.$message.error('');
                    return false
                }
                this.$nextTick(function() {
                    this.axios.post('http://localhost:8089/paperorganize/addpaper', resObj)
                    .then(res => {
                        console.log(res);
                        if(res.data.stat == 'ok'){
                            this.$message.success(res.data.msg);
                        }
                        this.loading = false;
                    })
                    .catch(res => {
                        this.loading = false;
                        console.log("error");
                    })

                })
            }
        },
        components: {
            lookInfo,
            selectproject1,
            selectproject2,
            selectproject3,
            selectproject4,
            selectproject5,
        },
        mounted() {
            this.userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
          /*  //
            this.getQuestionHandle()*/
            //
            bus.$on('id1', data => {
                this.singleSelect = data;
            });
            bus.$on('id2', data => {
                this.multipleSelect = data;
            });
            bus.$on('id3', data => {
                this.fillblank = data;
            });
            bus.$on('id4', data => {
                this.tureorfalse = data;
            });
            bus.$on('id5', data => {
                this.quesAndAns = data;
            });
        },
        watch: {
            //  `question` 
            type: function() {
                this.getQuestionHandle()
            }
        },
    }
</script>

<style scoped>
  .projectselect {
    display: inline-block;
    margin-top: 20px;
    margin-bottom: 10px;
  }

  .projectselect span {
    padding: 0 15px;
  }
    .title {
        font-size: 26px;
        font-family: ''
    }
    .point {
        margin-left: 15px;
    }
    .myinput {
        width: 100px;
        text-align: center;
    }
    .form-inline {
        padding-top: 5px;
        padding-left: 50px;
    }
    .select-type {
        width: 150px;
    }
    .product {
        margin: 30px auto;
        text-align: center;
    }
</style>



