<template>
    <el-dialog title="" :visible.sync="dialogFormEditVisible" :show-close="false">
            <el-form>
                <el-form-item>
                    <h3>ID</h3>
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
                <el-form-item>
                    <h3></h3>
                    <el-input v-model="form.courseId" disabled></el-input>
                </el-form-item>
                <el-form-item>
                    <h3></h3>
                    <el-input type="textarea" v-model="form.question"></el-input>
                </el-form-item>
                <el-form-item>
                    <h3>A</h3>
                    <el-input v-model="form.A"></el-input>
                </el-form-item>
                <el-form-item>
                    <h3>B</h3>
                    <el-input v-model="form.B"></el-input>
                </el-form-item>
                <el-form-item>
                    <h3>C</h3>
                    <el-input v-model="form.C"></el-input>
                </el-form-item>
                <el-form-item>
                    <h3>D</h3>
                    <el-input v-model="form.D"></el-input>
                </el-form-item>
                <el-form-item v-if="showMuilt">
                    <h3>E</h3>
                    <el-input v-model="form.E"></el-input>
                </el-form-item>

                <el-form-item >
                    <h3></h3>
                    <el-input type="textarea" v-model="form.answer"></el-input>
                </el-form-item>

                <el-form-item>
                    <h3></h3>
                    <el-input type="textarea" v-model="form.faq"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="clearImgs"> </el-button>
                <el-button type="primary" @click="handleEditClose"> </el-button>
            </div>
        </el-dialog>
</template>

<script>
    import bus from './BUS.vue'
    import { Loading } from 'element-ui'
     export default {
        data () {
            return {
              showMuilt:false,
                form:{},
                dialogFormEditVisible:false,
                question_fileList:[],
                answer_fileList:[],
                imgsrc:'',
                showImg:false,
                showTip:true,
                questionImgCounter:0,
                answerImgCounter:0,
                questionImgNameAry:[],
                answerImgNameAry:[],
                type:''
            }
        },
        methods:{
            clearImgs() {
                this.dialogFormEditVisible = false;
                this.question_fileList = [];
                this.answer_fileList = [];
                this.questionImgCounter = 0;
                this.answerImgCounter = 0;
                this.questionImgNameAry = [];
                this.answerImgNameAry = [];
                this.$refs.questionimgdiv.innerHTML = "";
                this.$refs.answerimgdiv.innerHTML = "";
            },
            handleEditClose(){  //
                this.dialogFormEditVisible = false;
                var loadingInstance = Loading.service({   //
                    target:'#container',
                    text:'...'
                });
                var params = new URLSearchParams();
                params.append('type',this.type);
                params.append('difficult',this.form.difficult);
                params.append('courseId',this.form.courseId);
                params.append('pointId',this.form.pointId);
                params.append('question',this.form.question);
                params.append('A',this.form.A);
                params.append('B',this.form.B);
                params.append('C',this.form.C);
                params.append('D',this.form.D);
                params.append('E',this.form.E);
                params.append('answer',this.form.answer);
                params.append('faq',this.form.faq);

                this.axios.post('http://localhost:8089/item/update',params)
                .then( res => {
                    loadingInstance.close();
                    this.$message.success('');
                })
                .catch( res => {
                    loadingInstance.close();
                    this.$message.error('');
                })
                this.clearImgs();
            },
            handleChange (file,fileList) {
                var loadingInstance2;
                var temp = this.form;   //thisvueform
                temp.imgName = new Date().getTime() + file.raw.name;
                var reader = new FileReader();
                reader.onloadend = function () {  //
                    temp.img = reader.result;
                    loadingInstance2.close();  //
                }
                if (file.raw) {
                    loadingInstance2 = Loading.service({   //
                        target:'.el-dialog',
                        text:'...'
                    });
                    reader.readAsDataURL(file.raw);   //
                } else {
                    console.log('error');
                }
            },
            appendImg (imgStr,divType) {
                let imgs = imgStr.split('-'); //
                for (let i = 0; i < imgs.length; i ++) {
                    let myimg = document.createElement('img');
                    myimg.src = 'http://localhost:8089/quest/layui/images/' + imgs[i];
                    myimg.className = 'myimg';
                    myimg.style = "width:50%;height:50%;margin:0 auto";
                    // DOM 
                    this.$nextTick(function () {
                        this.$refs[divType].appendChild(myimg);
                    })
                }
            },
            handleQuestionChange (file,fileList) { //
                this.handleImgChange('questionImgCounter','questionImgNameAry','question_','questionImg',file);
            },
            handleAnswerChange (file,fileList) {//
                this.handleImgChange('answerImgCounter','answerImgNameAry','answer_','answerImg',file);
            },
            handleImgChange(ImgCounter,imgNameAry,type,imgType,file) { //
                this[ImgCounter] ++;
                let loadingInstance2;
                let temp = this.form;   //thisvueform
                this[imgNameAry].push(type + new Date().getTime() + file.raw.name);
                let target = this;
                let reader = new FileReader();
                reader.onloadend = function () {  //
                    let Img = target[ImgCounter];
                    temp[imgType][Img] = reader.result;
                    loadingInstance2.close();  //
                }
                if (file.raw) {
                    loadingInstance2 = Loading.service({   //
                        target:'.el-dialog',
                        text:'...'
                    });
                    reader.readAsDataURL(file.raw);   //
                } else {
                    console.log('error');
                }
            },
            //
            reProductImg (imgType,fileList) {
                for (let i = 0; i < fileList.length; i ++) {
                    let loadingInstance2;
                    let temp = this.form;   //thisvueform
                    let target = this;
                    let reader = new FileReader();
                    reader.onloadend = function () {  //
                        temp[imgType][i+1] = reader.result;
                        loadingInstance2.close();  //
                    }
                    if (fileList[i].raw) {
                        loadingInstance2 = Loading.service({   //
                            target:'.el-dialog',
                            text:'...'
                        });
                        reader.readAsDataURL(fileList[i].raw);   //
                    } else {
                        console.log('error');
                    }
                }
            },
            //
            removeImgName (imgName,imgNameArr) {
                for (let i = 0; i < this[imgNameArr].length; i ++) {
                    if (this[imgNameArr][i].indexOf(imgName) > -1) {
                        this[imgNameArr].splice(i,1);
                    }
                }
            },
            //
            handleQuestionImgRemove(file,fileList) {
                this.questionImgCounter --;
                this.form.questionImg = {};
                if (this.questionImgCounter >= 0) {
                    this.removeImgName(file.name,'questionImgNameAry');
                    //
                    this.reProductImg('questionImg',fileList);
                }
            },
            //
            handleAnswerImgRemove(file,fileList) {
                this.answerImgCounter --;
                this.form.answerImg = {};
                if (this.answerImgCounter >= 0) {
                    this.removeImgName(file.name,'answerImgNameAry');
                    //
                    this.reProductImg('answerImg',fileList);
                }
            }
        },
        mounted() {    //
            bus.$on('edit', data => {   //
                this.dialogFormEditVisible = data.flag;
                data.value.questionImg = {};
                data.value.answerImg = {};
                this.showMuilt = data.muilt;
                this.form = data.value;
                this.type = data.region;
                this.handleImgs();
            })
        }
    }
</script>

<style scoped>
    .showimg {
        width: 300px!important;
        margin: 0;
        float: left!important;
        margin-left: 15px;
    }

    .tip{
        color:grey;
        display: block;
        padding: 15px ;
        font-size: 16px;
    }

    .myimg{
        display: block;
        width:200px;
        height: 150px;
        margin-top: 15px;
    }
</style>




