<template>
    <div>
        <div class="myaction">
            <div class="first">
                <div class="left">
                <!--<el-form :inline="true" class="demo-form-inline">
                        <el-form-item label="" class="myelform">
                            <el-input v-model="searchContent" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item  class="myelform">
                            <el-button type="primary" @click="onSubmit" icon="el-icon-search"></el-button>
                        </el-form-item>
                    </el-form>-->
                </div>
                <div class="right">

                </div>
                <div class="clearfix"></div>
            </div>
            <div class="clearfix"></div>

            <div class="bottom">
                <div class="left">

                    <el-button icon="el-icon-plus" type="primary" @click="handerAddSubject"></el-button>
                </div>

                <div class="clearfix"></div>
            </div>
            <div class="clearfix"></div>
        </div>

        <div class="projectselect">
            <span></span>
            <el-select v-model="type" placeholder="..">
                <el-option label="" value="10001" selected></el-option>
                <el-option label="" value="10002"></el-option>
                <el-option label="" value="10003"></el-option>
                <el-option label="" value="10004"></el-option>
                <el-option label="" value="10005"></el-option>
            </el-select>
        </div>

        <el-table
        :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
        @selection-change="handleSelectionChange"
        border
        stripe
        style="width: 100%"
        max-height="449"
        highlight-current-row
        :cell-style="styleFunc">
            <el-table-column
            type="selection"
            width="55"
            align="center">
            </el-table-column>
            <el-table-column
            prop="id"
            label="ID"
            width="60"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="knowledge"
            label=""
            width="150"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="courseId"
            label=""
            width="120"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="question"
            label=""
            width="250"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="answer"
            label=""
            width="120"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
              v-if="showOption"
            prop="optioncontent"
            label=""
            width="350"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            prop="faq"
            label=""
            width="120"
            align="center"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
            label=""
            width="182"
            align="center">
                <template slot-scope="scope">
                    <el-tooltip class="item" effect="light" content="" placement="bottom-end">
                        <el-button @click="handleLook(scope.row)" type="info" size="mini"><i class="el-icon-view"></i></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="light" content="" placement="bottom-end">
                        <el-button  size="mini" type="primary" @click="handleEdit(scope.row)"><i class="el-icon-edit"></i></el-button>
                    </el-tooltip>
                    <el-tooltip class="item" effect="light" content="" placement="bottom-end">
                        <el-button  size="mini" type="danger" @click="handleDel(scope.row.id)"><i class="el-icon-delete"></i></el-button>
                    </el-tooltip>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <div class="block">
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 30]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="count">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import bus from './BUS.vue'

    export default {
        data () {
            return {
              showOption:true,
                currentPage:1,
                pageSize:5,
                count:0,
                currentLimit:200,
                searchContent:"",
                multipleSelection: [],
                form:"",
                tableData:[],
                isSerach:false,  //
                type:'',
                isLast:false,
                loading:true,
              showMuilt:false
            }
        },
        methods:{
            baiDU(){   //
                this.axios.get('http://localhost:8089/paperorganize/baidu')
                .then(function (response) {
                console.log(response);
                })
                    },

            onSubmit(){  //
                if ( this.searchContent == "") {
                    this.$message.error('');
                }else{
                    this.loading = true;
                    this.isSerach = true;
                    this.axios.get('http://localhost:8089/searchQuestions',{
                        params: {
                            data:this.searchContent
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = [];
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                        this.currentPage = 1;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                }
            },
            handleLook(param){  //lookinfo
                bus.$emit("look", {flag:true,value:param,choice:this.showOption});
            },
            handleEdit(param){   //editinfo
              if (this.type ==10002){
                this.showMuilt = true;
              }
                bus.$emit("edit", {flag:true,value:param,type:this.type,muilt: this.showMuilt});
            },
            handleDel(id){   //
                this.$confirm(', ?', '',{
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.loading = true;
                    let params = new URLSearchParams();
                        params.append("type",this.type);
                        params.append("id",id);
                        console.log(params);
                        this.axios.post('http://localhost:8089/item/delete',params)
                        .then( res => {
                            this.$message.success('');
                            //
                            this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                                params: {
                                    type:this.type
                                }
                            })
                            .then( res => {
                                this.loading = false;
                                this.tableData = [];
                                this.tableData = res.data.data;
                                this.count = res.data.count;
                            })
                            .catch( res => {
                                this.loading = false;
                                console.log("error");
                            })
                        })
                        .catch( res => {
                            this.loading = false;
                            this.$message.error('');
                        })
                }).catch(() => {
                });
            },
            handerAddSubject() {  //
                if ( this.currentPage == Math.ceil( this.count / this.currentLimit) ) //
                    this.isLast = true;
                if (this.type ==10002){
                  this.showMuilt = true;
                }
                if(this.type == '')
                {
                  this.$message.error('')
                }
                else
                  bus.$emit("addSubject", {flag:true,type:this.type,isLast:this.isLast,option:this.showOption,muilt:this.showMuilt});
            },
            handleSizeChange(val) {  //,
                this.loading = true;
                this.pageSize = val;
                if (this.isSerach) {    //
                    this.axios.get('http://172.20.10.14:8089/searchQuestions',{
                        params: {
                            data:this.searchContent
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = [];
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                        this.currentPage = 1;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                } else {   //
                    this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                    params: {
                        type:this.type
                    }
                })
                .then( res => {
                    this.loading = false;
                    this.tableData = res.data;
                  this.count = res.data.data.length;
                })
                .catch( res => {
                    this.loading = false;
                    console.log("error");
                })
                }
            },
            handleCurrentChange(val) {  //,
                this.loading = true;
                this.currentPage = val;
                if (this.isSerach) {   //
                    this.axios.get('http://172.20.10.14:8089/searchQuestions',{
                        params: {
                            data:this.searchContent
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = [];
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                } else {
                    this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                        params: {
                            type:this.type
                        }
                    })
                    .then( res => {
                        this.loading = false;
                        this.tableData = res.data.data;
                      this.count = res.data.data.length;
                      this.currentPage = val;
                    })
                    .catch( res => {
                        this.loading = false;
                        console.log("error");
                    })
                }
            },
            styleFunc(row,rowIndex){   //
                return {padding:'5px 0',hieght:'23px',lineHeight:'23px'}
            },
            handleSelectionChange(val) {   //
                this.multipleSelection = val;
            },
            batchDel () {  //
                var delTarget = this.multipleSelection;
                var count = delTarget.length;
                var str = "";
                if (count == 0) {
                    this.$message.error("");
                    return false;
                } else {
                    for ( var i = 0 ; i < count; i ++) {//id
                        str += delTarget[i].id + ",";
                    }
                    this.$confirm(', ?', '',{
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() => {
                        this.loading = true;
                        var params = new URLSearchParams();
                        params.append("type",this.type);
                        params.append("data",str);
                        console.log(params);
                        this.loading = true;
                        this.axios.post('http://localhost:8089/item/bachDelete',params)
                        .then( res => {
                                this.$message.success('');
                                //
                                this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                                    params: {

                                        type:this.type
                                    }
                                })
                                .then( res => {
                                    this.loading = false;
                                    this.tableData = [];
                                    this.tableData = res.data.data;
                                  this.count = res.data.data.length;
                                })
                                .catch( res => {
                                    this.loading = false;
                                    console.log("error");
                                })
                        })
                        .catch( res => {
                            this.loading = false;
                            this.$message.error('');
                        })
                    }).catch(() => {
                    })
                }
            }
        },
        mounted(){ //
          bus.$on('addClose',function (data) {
            this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
              params: {

                type:data.type
              }
            })
              .then( res => {
                this.tableData = res.data.data;
                this.count = res.data.data.length;
                this.currentPage = Math.ceil(this.count/this.pageSize)
              })
              .catch( res => {
                console.log("error");
              })
          })
            var target = this;
            //
            bus.$on('isLast', function (data) {
                this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                    params: {

                        type:target.type
                    }
                })
                .then( res => {
                    target.tableData = res.data.data;
                  target.count = res.data.data.length;
                })
                .catch( res => {
                    console.log("error");
                })
            });
        },
        watch:{    //
            type: function (newValue) {
              if (newValue != 10001 && newValue != 10002){
                console.log(newValue);
                this.showOption = false;
              }
                this.loading = true;
                this.currentPage = 1;
                this.axios.get('http://localhost:8089/paperorganize/getQuestions',{
                    params: {
                        type:newValue
                    }
                })
                .then( res => {
                  if(res.data.data.length != 0) {
                    this.loading = false;
                    this.tableData = res.data.data;
                    this.count = res.data.data.length;
                  }
                  else{
                    this.tableData = [];
                    this.$message.error("");
                  }

                })
                .catch( res => {
                this.loading = false;
                    console.log("error");
                })
            }
        }
    }
</script>

<style scoped>

    .myaction {
        margin-top: 15px;
    }

    .first {
        vertical-align: top;
    }

    .left {
        margin-left: 10px;
    }

    .left div {
        display: inline-block;
    }

    .left span,
    .left div,
    .left button {
        float: left;
        margin-left: 20px;
    }

    .left span {
        display: inline-block;
        height: 38px;
        line-height: 38px;
    }

    .right {
        float: right;
        margin-right: 25px;
    }

    .bottom {
        margin: 10px 20px;
    }

    .bottom button {
        margin-right: 10px;
    }
    .clearfix{
        clear:both;
    }

    .myelform{
        margin-bottom: 5px;
    }

    .block{
        margin:10px;
    }

    .projectselect {
        margin-top: 20px;
        margin-bottom: 10px;
    }

    .projectselect span {
        padding: 0 15px;
    }
</style>




