<template>
    <div id="upload">
        <div>
            <el-select v-model="region" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
                <el-option label="" value="3"></el-option>
                <el-option label="" value="4"></el-option>
            </el-select>
        </div>
      <el-upload
        :show-file-list="true"
        accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        action="/employee/basic/importEmp"
        :data="{id:region}"
        :on-success="fileUploadSuccess"
        :on-error="fileUploadError" :disabled="fileUploadBtnText==''"
        :before-upload="beforeFileUpload" style="display: inline">
        <el-button size="mini" type="success" :loading="fileUploadBtnText==''"><i class="fa fa-lg fa-level-up"
                                                                                      style="margin-right: 5px"></i>{{fileUploadBtnText}}
        </el-button>
        <div slot="tip" class="el-upload__tip">.xls/.xlsx500kb</div>
      </el-upload>
        <el-upload
        class="upload-demo"
        drag
        accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
        :limit="number"
        auto-upload
        action="http://localhost:8089/upload"
        :on-success="handleSuccess"
        :on-error="handleError"
        multiple
        :data="{id:region}">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">doc/doxc</div>
        </el-upload>
    </div>
 <!-- <el-button @click="getPaper"></el-button>-->
</template>

<script>
    export default {
        data(){
            return {
                region:'',
                fileUploadBtnText:'',
                dataObj:{
                    id:this.region
                }
            }
        },
        methods:{
          fileUploadSuccess(response, file, fileList){
            if (response) {
              this.$message({type: response.status, message: response.msg});
            }
            this.loadEmps();
            this.fileUploadBtnText = '';
          },
          fileUploadError(err, file, fileList){
            this.$message({type: 'error', message: "!"});
            this.fileUploadBtnText = '';
          },
          beforeFileUpload(file){
            this.fileUploadBtnText = '';
          },
           handleSuccess(){
               this.$message.success('');
           },
           handleError(){
               this.$message.error('');
           },
         /* getPaper(){
            this.axios.get('http://localhost:8089/download/download',{
              params:{
              id:1001
              }
            })
              .then
          }*/
        }
    }
</script>

<style scoped>
    #upload{
        margin-top: 150px;
        margin-left: 350px;
    }
    .upload-demo{
        margin-top:15px;
    }
</style>




